.deckss{    
    padding: 20px;
    background-color: rgb(239,225,189);
    margin-top: 0;
    
}


.cardss{
    width:30%;
    padding: 10px;
    text-align: center;
    padding-bottom: 0;
    background-color: #fff;
}
.year {
    background-color: darkslategrey;
    width: 150px;
    text-align: center;
    color: #fff;
    border-radius: 5px;
}
.card-body{
    padding-top: 0px;
    align-self: center;
    padding-bottom: 0px;
    background-color: #fff;
}
.card-body > a {
    color: rgb(117, 122, 67);
}
.project_header{
    text-align: center;
    background-color: rgb(239,225,189);
    margin-bottom: 0;
}
.whole-project{
    background-color: rgb(239,225,189);
}
.cardss:hover{
    transform: translate(-0.5rem, -3.5rem);
}

@media only screen and (max-width: 1000px){
    .deckss{
        display:flex;
        flex-direction: column;
        align-items: center;
    }

    .cardss{
        width:50%;
        padding: 10px;
        text-align: center;
        margin-top: 50px;
        background-color: #fff;
    }
}